const paths = {
  plane:
    'M187.57,0C291.16,0,375.13,67.75,375.13,151.32c0,33.35-13.41,64.17-36.07,89.17l34.85,86.65L226.21,297.46c-15.43.42-32.72,4.16-50,4.61-2,.05-3.92.08-5.86.08C69.88,302.15,0,233.3,0,151.32,0,67.75,84,0,187.57,0m0,41C106.76,41,41,90.51,41,151.32c0,62.6,55.57,109.8,129.28,109.8q2.38,0,4.78-.06c6.93-.18,14.5-1.12,22.52-2.12,8.87-1.1,18-2.24,27.49-2.5l4.64-.13,4.56.92,73.19,14.71L301,255.81,291.41,232l17.25-19c11.61-12.81,25.44-33.89,25.44-61.62C334.1,90.51,268.37,41,187.57,41Z',
  heart:
    'M350,35.1h-18.4V29.66A29.75,29.75,0,0,0,301.91,0h-.44a29.75,29.75,0,0,0-29.66,29.66V35.1h-164V29.66A29.75,29.75,0,0,0,78.13,0h-.44A29.75,29.75,0,0,0,48,29.66V35.1H29.66A29.75,29.75,0,0,0,0,64.76V348.32A29.75,29.75,0,0,0,29.66,378H350a29.75,29.75,0,0,0,29.66-29.66V64.76A29.75,29.75,0,0,0,350,35.1Zm-66.29-5.44a17.81,17.81,0,0,1,17.79-17.79h.44a17.83,17.83,0,0,1,17.79,17.79V82.42a17.83,17.83,0,0,1-17.79,17.79h-.44a17.81,17.81,0,0,1-17.79-17.79Zm-223.78,0A17.81,17.81,0,0,1,77.69,11.86h.44A17.83,17.83,0,0,1,95.93,29.66V82.42a17.83,17.83,0,0,1-17.79,17.79h-.44A17.81,17.81,0,0,1,59.9,82.42ZM350,354.25H29.66a6,6,0,0,1-5.93-5.93V139.83H355.9V348.32A6,6,0,0,1,350,354.25Z',
  bookmark:
    'M357,0H102C73.95,0,51,22.95,51,51v408l178.5-76.5L408,459V51C408,22.95,385.05,0,357,0z',
}

const pathIds = Object.keys(paths)

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1,
  }).pipe(interpolate(from, to))

const Icon = posed.path(
  pathIds.reduce((config, id) => {
    config[id] = {
      d: paths[id],
      transition: morphTransition,
    }

    return config
  }, {}),
)

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.8 },
})

class IconMorph extends React.Component {
  state = { pathIndex: 0 }

  gotoNext = () => {
    const { pathIndex } = this.state
    const nextIndex = pathIndex + 1
    this.setState({
      pathIndex: nextIndex > pathIds.length - 1 ? 0 : nextIndex,
    })
  }

  render() {
    return (
      <StyledBody>
        <svg
          width="120"
          height="120"
          viewBox="0 0  375.13 327.15"
          fill="rgb(70, 200, 120)"
        >
          <Icon pose={pathIds[this.state.pathIndex]} />
        </svg>
        <NextButton onClick={this.gotoNext}>
          Refreshingly Simple Text Message Appointment Reminders
        </NextButton>
      </StyledBody>
    )
  }
}

export default IconMorph
